import React, { Component } from 'react';
import uuid from 'uuid';
import Materialize from 'materialize-css';

class AddCards extends Component {
    constructor() {
        super();
        this.state = {
            newProduct: {},
        }
    }
    setNewCard(){
        if(this.refs.price.value !== '' && this.refs.title.value !== '') {
            var dollar = this.refs.price.value.split('$');
            console.log(dollar[0]);
            if(dollar[0] === '') {
                this.setState({
                    newProduct : {
                        id: uuid.v4(),
                        title: (this.refs.title.value),
                        features: (this.refs.features.value),
                        description: (this.refs.desc.value),
                        price : (this.refs.price.value),
                        loves: 0
                    }
                }, function(){
                    this.props.cards(this.state.newProduct);
                });
            }
            else {
                Materialize.toast('You entered wrong price', 2000, 'rounded')
            }
        }
        else {
            Materialize.toast('Please fill the empty fields', 2000, 'rounded')
        }
    }
    render() {
        return(
            <div id="modal1" className="modal">
                <div className="modal-content grey lighten-5">
                    <h4 className="grey-text text-darken-2">Information about your product</h4>
                    <div className="row">
                        <form className="col s12">
                            <div className="row">
                                <div className="input-field col s12">
                                    <input id="first_name" type="text" className="validate black-text" ref = 'title'/>
                                    <label htmlFor ="first_name">Product Title</label>
                                </div>
                                <div className="input-field col s12">
                                    <textarea id="textarea1" className="materialize-textarea black-text" ref = 'features'></textarea>
                                    <label htmlFor="features">Features</label>
                                </div>
                                <div className="input-field col s12">
                                    <textarea id="textarea1" className="materialize-textarea black-text" ref = 'desc'></textarea>
                                    <label htmlFor="description">Description</label>
                                </div>
                                <div className="input-field col s12">
                                    <input id="first_name" type="text" className="validate black-text" ref = 'price' placeholder="$50"/>
                                    <label htmlFor="price">Price</label>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
                <div className="modal-footer grey lighten-3">
                    <a href="#!" className="modal-action modal-close waves-effect waves-green btn-flat cyan-text text-darken-4" onClick={() => this.setNewCard()}>Add</a>
                </div>
            </div>
        );
    }
}
export default AddCards;


