import React, { Component } from 'react';


class Description extends Component {
    render() {
        return(
            <div id="modal2" className="modal">
                <div className="modal-content grey lighten-5">
                    <ul className="collection with-header grey-text text-darken-2">
                        <li className="collection-header"><h4 className="grey-text text-darken-2">{this.props.data.title}</h4></li>
                        <li className="collection-item">
                            {this.props.data.features}
                        </li>
                        <li className="collection-item">{this.props.data.description}</li>
                        <li className="collection-item">{this.props.data.price}</li>
                    </ul>
                </div>
                <div className="modal-footer">
                    <a className="modal-action modal-close waves-effect waves-green btn-flat">Hide</a>
                </div>
            </div>
        );
    }
}

export default Description;


