import React, { Component } from 'react';
import Goods from './Goods';
import Description from './Description';
import '../App.css';
import Materialize from 'materialize-css';

class Cards extends Component {
    constructor() {
        super();
        this.state = {
            payoff : [],
            description : [],
            total : 0
        }
    }
    addToPay(data) {
        const items = this.state.payoff.slice();
        if (items.findIndex(x => x === data) === -1) {
            items.push(data);
            var num = data.price.split('$');
            this.setState({
                payoff : items,
                total : this.state.total + +num[1],
            });
            this.props.payoff(this.state.payoff.length+1);
        }
        else {
            Materialize.toast('This item is already added!', 2000, 'rounded')
        }
    }
    reactLove(data) {
        this.props.loveReact(data);
    }
    description(data) {
        this.setState({description : data});
        this.props.description();
    }
    render() {
        const cards = this.props.cards.map((cards) => {
            return(
                <div className="col s12 m4" key={cards.id}>
                    <div className="card cyan darken-3 small hoverable">
                            <div className="card-image">
                                <img src="http://via.placeholder.com/200x80" alt="Loading"/>
                                <span className="card-title">{cards.title}</span>
                            </div>
                            <div className="card-content">
                                <p className="truncate">{cards.description}</p>
                            </div>
                            <div className="card-action">
                               <i className="fa fa-heart icon" onClick={this.reactLove.bind(this , cards)}></i><span>{cards.loves}</span>
                                <a href="#modal3" className="waves-effect waves-light btn-floating right deep-orange modal-trigger cards-buttons" onClick={this.description.bind(this, cards)}><i className="fas fa-bullhorn"></i></a>
                                <a className="waves-effect waves-light btn-floating right green lighten-1 cards-buttons" onClick={this.addToPay.bind(this, cards)}><i className="fa fa-plus"></i></a>
                            </div>
                    </div>
                </div>
            );
        });
        return(
            <div>
                <div className="row">
                    {cards}
                </div>
                <Goods payoff = {this.state.payoff} total = {this.state.total}/>
                <Description data = {this.state.description}/>
            </div>
            
        );
    }
}

export default Cards;


