import React, { Component } from 'react';

class Goods extends Component {
    constructor() {
        super();
        this.state = {
            empty : false,
        }
    }
    componentWillReceiveProps(nextProps) {
        if(nextProps.payoff  !== this.props.payoff)
            this.setState({empty: true})
    }
    render() {
        let items = this.props.payoff.map((payoff) => {
                return (
                    <li key= {payoff.id}>
                        <a><i className="fa fa-check"></i>{payoff.title}<p className="listPrice">{payoff.price}</p></a>
                    </li>
            );
        });
        if(this.state.empty) {
            return (

                    <ul id='dropdown1' className='dropdown-content'>
                        {items}
                        <li><a href=""><i className="fa fa-dollar-sign left"></i>{this.props.total} Total<i className="fa fa-arrow-right right checkout-icon"></i></a></li>
                    </ul>
            );
        } else {
            return(
                <ul id='dropdown1' className='dropdown-content'>
                        <li><span>Empty</span></li>
                </ul>
            );
        }
    }
}
export default Goods;


