import React, { Component } from 'react';

class Feedback extends Component {
    render() {
        return(
            <div id="modal3" className="modal">
                <div className="modal-content grey lighten-5">
                <h4 className="grey-text text-darken-2">Keep in touch with us</h4>
                <div className="row">
                    <form className="col s12">
                        <div className="row">
                            <div className="input-field col s12">
                                <input id="first_name" type="text" className="validate black-text"/>
                                <label htmlFor ="first_name">Your name</label>
                            </div>
                            <div className="input-field col s12">
                                <textarea id="textarea1" className="materialize-textarea black-text"></textarea>
                                <label htmlFor="description">Feedback</label>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
                <div className="modal-footer grey lighten-3">
                    <a className="modal-action modal-close waves-effect waves-green btn-flat grey-text text-darken-3">Send</a>
                </div>
            </div>
        );
    }
}
export default Feedback;


